/* stylelint-disable-line at-rule-no-unknown */
/* ================== ТИПОГРАФИКА ================== */
.page {
  width: 100%;
  font-size: 16px;
  font-family: "SF UI Text", sans-serif;
  color: #3B3B3B;
}
.page * {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}
.page body {
  display: flex;
  flex-direction: column;
  align-items: center;
  min-width: 100vw;
  min-height: 100vh;
  overflow-x: hidden;
}
.page__inner {
  display: flex;
  flex: 1;
  flex-direction: column;
  width: 100%;
}
.page__content {
  flex: 1;
}
.page .xs-hidden {
  display: none;
}
@media screen and (min-width: 768px) {
  .page .xs-only {
    display: none;
  }
  .page .xs-hidden {
    display: grid;
  }
}

.header {
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  gap: 14px;
  width: 100%;
  padding: 20px;
  background-color: #205871;
  color: #ffffff;
}
.header__help {
  display: inline-flex;
  margin-left: auto;
  font-size: 0.75rem;
}
.header__help-link {
  transition: opacity 0.25s ease-in;
  /* stylelint-disable-next-line plugin/selector-bem-pattern */
  margin-left: 0.5em;
}
.header__help-link:hover {
  opacity: 0.65;
}
.header__help-link, .header__help-link:visited, .header__help-link:active {
  text-decoration: none;
  color: #08d1d4;
  text-decoration: none;
}

@media screen and (min-width: 600px) {
  .header__help {
    margin-left: unset;
    font-size: 1rem;
  }
}
.main-nav {
  display: flex;
  justify-content: space-between;
  width: 100%;
}
.main-nav__list {
  display: flex;
  flex-wrap: wrap;
  gap: 14px;
  width: 100%;
  list-style: none;
}
.main-nav__item {
  display: inline-flex;
  align-items: center;
  min-width: max-content;
  height: 20px;
  color: #ffffff;
}
.main-nav__item:not(:first-of-type) {
  margin-left: auto;
}
.main-nav__link {
  display: inline-flex;
  position: relative;
  font-size: 0.75rem;
  color: #ffffff;
  text-decoration: none;
}
.main-nav__link--active {
  color: #08d1d4;
}

@media screen and (min-width: 600px) {
  .main-nav {
    width: max-content;
  }
  .main-nav__list {
    gap: 24px;
  }
  .main-nav__item {
    height: 27px;
  }
  .main-nav__item:not(:first-of-type) {
    margin-left: unset;
  }
  .main-nav__link {
    font-size: 1rem;
  }
}
.main-top {
  display: flex;
  flex-direction: column;
  justify-content: center;
  min-height: 375px;
}
.main-top__text-wrapper {
  display: flex;
  flex-direction: column;
  padding: 16px;
}
.main-top__title {
  margin-bottom: 1rem;
  font-size: 1.875rem;
  font-weight: 800;
  text-transform: uppercase;
}
.main-top__text {
  margin-bottom: 1.125em;
  font-size: 1rem;
  line-height: 1.5em;
}
.main-top__img-wrapper {
  display: none;
}

@media screen and (min-width: 768px) {
  .main-top {
    display: grid;
    grid-template-columns: 50% 50%;
    min-height: 553px;
  }
  .main-top__text-wrapper {
    justify-content: center;
    padding: 40px;
  }
  .main-top__text-col {
    display: flex;
    flex-direction: column;
    width: max-content;
    max-width: 100%;
    margin-left: auto;
  }
  .main-top__title {
    max-width: 494px;
    margin-bottom: 2.125rem;
    font-size: 2rem;
  }
  .main-top__text {
    max-width: 494px;
    margin-bottom: 2rem;
  }
  .main-top__picture {
    display: flex;
    align-items: flex-end;
    justify-content: center;
    width: 100%;
    height: 100%;
  }
  .main-top__img-wrapper {
    display: flex;
    align-items: flex-end;
    justify-content: center;
    background-color: #ffbda9;
  }
}
@media screen and (min-width: 1920px) {
  .main-top__text-col {
    margin-left: auto;
  }
}
.main-bottom {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  gap: 78px;
  margin: 1rem;
  min-height: 410px;
  background-color: #205871;
  border-radius: 30px;
}
.main-bottom__text {
  max-width: 295px;
  font-size: 1.25rem;
  line-height: 1.5;
  font-weight: 800;
  text-align: center;
  color: #ffffff;
}

.download-btn {
  transition: opacity 0.25s ease-in;
  /* stylelint-disable-next-line plugin/selector-bem-pattern */
  display: flex;
  justify-content: center;
  align-items: center;
  width: max-content;
  padding: 16px 25px;
  background-color: #08d1d4;
  border-radius: 40px;
  border: none;
  color: #ffffff;
  cursor: pointer;
}
.download-btn:hover {
  opacity: 0.65;
}
.download-btn--bottom {
  padding: 24px 29px;
  background-color: #ffffff;
  color: #205871;
}

@media screen and (min-width: 768px) {
  .download-btn {
    padding: 24px 58px;
  }
}
.policy {
  display: flex;
  flex-direction: column;
  padding: 32px 16px;
}
.policy__text-wrapper {
  display: flex;
  flex-direction: column;
  max-width: 700px;
}
.policy__title {
  display: flex;
  position: relative;
  width: 100%;
  margin-bottom: 1em;
  font-size: 1.875rem;
  line-height: 1.2em;
  font-weight: 700;
  word-wrap: break-word;
}
.policy__title::after {
  content: "";
  display: block;
  position: absolute;
  left: 0;
  bottom: -5px;
  width: 100%;
  height: 1px;
  background-color: #dcdde2;
}
.policy__header {
  margin: 1em 0;
  font-size: 1rem;
  line-height: 1.5em;
  font-weight: 700;
}
.policy__header2 {
  margin: 1em 0;
  font-size: 1rem;
  line-height: 1.5em;
  font-weight: 700;
}
.policy__text {
  margin: 0 0 1em;
  font-size: 0.875rem;
  line-height: 1.5em;
}

.features {
  display: flex;
  flex-direction: column;
}

.feature {
  display: flex;
  flex-direction: column;
}
.feature__text-wrapper {
  display: flex;
  flex-direction: column;
  padding: 18px 12px 32px;
  background-color: #f6f6f6;
}
.feature__text-wrapper--white {
  background-color: #ffffff;
}
.feature__ico {
  width: 100px;
  height: 100px;
}
.feature__title {
  margin: 18px 0 16px;
  font-size: 1.5rem;
  line-height: 1.2em;
  font-weight: 800;
  color: #222222;
}
.feature__list {
  display: flex;
  flex-direction: column;
  gap: 1rem;
  padding-left: 30px;
}
.feature__list--two-col {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 46px;
}
.feature__item {
  font-weight: 500;
  font-size: 1.125rem;
  line-height: 1.5em;
  color: #747474;
}
.feature__xs-img-wrapper {
  display: flex;
  align-items: flex-end;
  justify-content: center;
  height: 455px;
  padding: 2rem 0 0;
  background-color: #ffbda9;
}
.feature__xs-img-wrapper picture {
  width: auto;
  height: 100%;
  object-position: bottom center;
}
.feature__xs-img {
  width: auto;
  height: 100%;
  object-position: bottom center;
}
.feature__img-wrapper {
  display: none;
}
.feature__img-group-wrapper {
  display: none;
}

@media screen and (min-width: 768px) {
  .feature__xs-img-wrapper {
    display: none;
  }
  .feature__first-text-block {
    justify-items: center;
    background-color: #f6f6f6;
    padding: 22px 0 8px;
  }
  .feature__first-text-block-wrapper {
    display: grid;
    justify-items: center;
    grid-template-columns: 387px 387px;
    row-gap: 0;
    column-gap: 32px;
  }
  .feature__first-text-block-wrapper .feature__text-wrapper:first-child {
    grid-row: span 2;
  }
  .feature__first-text-block-wrapper--last {
    grid-template-columns: 1fr;
    grid-template-rows: 1fr;
  }
  .feature__text-wrapper {
    width: 387px;
  }
  .feature__text-wrapper--last {
    width: 560px;
  }
  .feature__img-group-wrapper {
    display: flex;
    align-items: flex-end;
    justify-content: center;
    grid-auto-columns: auto;
    min-height: 376px;
    padding-top: 32px;
    background-color: #ffbda9;
  }
  .feature__img-group-wrapper picture {
    display: flex;
  }
}
@media screen and (min-width: 1920px) {
  .feature__img-group-wrapper {
    min-height: 571px;
    height: 571px;
  }
}