.page {
  width: 100%;
  font-size: $font-size;
  font-family: 'SF UI Text', sans-serif;
  color: $text-color;

  * {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
  }

  body {
    display: flex;
    flex-direction: column;
    align-items: center;
    min-width: 100vw;
    min-height: 100vh;
    overflow-x: hidden;
  }

  &__inner {
    display: flex;
    flex: 1;
    flex-direction: column;
    width: 100%;
  }

  &__content {
    flex: 1;
  }

  .xs-hidden {
    display: none;
  }

  @media screen and (min-width: $screen-sm) {
    .xs-only {
      display: none;
    }

    .xs-hidden {
      display: grid;
    }
  }
}
